body {
  background-color: #f0f0f0;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

#gameContainer {
  background-color: #fff;
  border-radius: 10px;
  width: 80%;
  max-width: 600px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

h1 {
  color: #333;
  text-align: center;
}

#guessContainer {
  background-color: #f9f9f9;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  padding: 15px;
  display: flex;
}

#inputs {
  justify-content: space-between;
  gap: 10px;
  display: flex;
}

#alreadySolved {
  text-align: center;
  background-color: #f9f9f9;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  padding: 15px;
  font-weight: bold;
  display: none;
}

.coefficient-input {
  flex-direction: column;
  flex: 1;
  align-items: center;
  display: flex;
}

.coefficient-input label {
  margin-bottom: 5px;
  font-weight: bold;
}

input {
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 3px;
  flex: 1;
  width: 60px;
  padding: 5px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  flex-basis: 100%;
  margin-top: 10px;
  padding: 10px 20px;
  transition: background-color .3s;
}

button:hover {
  background-color: #45a049;
}

#hints {
  justify-content: space-around;
  margin-top: 20px;
  display: flex;
}

.hint {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.hint-icon {
  margin-top: 5px;
  font-size: 24px;
}

#attempts {
  text-align: center;
  justify-content: center;
  margin-top: 15px;
  display: flex;
}

#stats, #countdown {
  text-align: center;
  justify-content: center;
  margin-top: 15px;
  display: none;
}

#quadraticChart {
  overflow-wrap: break-word;
